.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ipSaXD {
  color: blue;
  background-color: white;
  border: solid blue 1px;
}

.sidebar ul {
  color: black;
  list-style-type: none; /* Remove bullets */
  margin: 0;
  padding: 0;
}

.sidebar ul ul {
  color: #444;
  list-style-type: none; /* Remove bullets */
  margin: 0;
  padding-left: 20px;
}

.sidebar li {
  width: 100%;
}

.sidebar a:hover {
  background-color: white;
  color: black;
}

.sidebar a {
  color: black;
  display: block;
  padding: 10px;
  cursor: pointer;
}

.sidebar .sidebarTitle {
  color: #444444;
  display: block;
  margin-top: 10px;
  padding-top: 20px;
  border-top: 1px #ddd solid;
}

.sidebarTitle {
  margin-top: 20px;
  padding: 10px;
}

.sidebar .sidebarTitle::before {
  content: "- ";
}